<template>
  <div class="dynamic-follow-content-view">
    <div class="top-tip">
      <div class="top-text-box">
        <div class="top-text-black">发现糗友</div>
        <div class="top-text-lightblack">
          有意思的糗友都在这里，粉ta，收获友谊
        </div>
      </div>
      <div class="top-img"></div>
    </div>
    <div class="content-box">
      <div class="content-list-wrap" v-for="item in contentData" :key="item.id">
        <dynamicCom
          :contentData="item"
          @toLike="toLike(item)"
          @getFollowState="getFollowState(item)"
          @addShareCount="addShareCount(item)"
          @deleteData="deleteData(item)"
          @getHeight="getHeight($event)"
          @goTocomment="goTocomment(item)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import dynamicCom from "@/components/DynamicCom/dynamicCom.vue";
import { getDynamicFollowContent } from "@/api/dynamic";
export default {
  name: "DynamicFollowContentView",
  components: {
    dynamicCom,
  },
  props: {
    scrollTop: {},
  },
  data() {
    return {
      //数据
      contentData: [],
      //总高度
      TotalHeight: 0,
      //加载状态
      loading: true,
      //刷新状态
      refreshState: false,
      //所有容器的单个高度
      HeightArr: [],
      //获取数据的id
      dataId: 0,
      //刷新时获取的数据
      refreshData: [],
      //定时器延迟拿取数据
      timer: null,
    };
  },
  watch: {
    scrollTop(newval, oldval) {
      let Bottom = null;
      if (this.TotalHeight > newval) {
        Bottom = this.TotalHeight - newval;
      }
      // console.log(Bottom);
      if (this.contentData.length > 0) {
        let autoplayIndex = Math.round(
          ((newval - 100 > 0 ? newval - 100 : 0) / this.TotalHeight) * 10
        );
        this.contentData.forEach((item, index) => {
          if (autoplayIndex == index) {
            item.isAutoplay = true;
          } else {
            item.isAutoplay = false;
          }
        });
      }
      if ((Bottom > 0 && Bottom < 500 && newval > oldval) || Bottom == null) {
        if (this.loading) {
          this.loading = false;
          this.getDynamicFollowContentData(1);
        }
      }
      // console.log(this.contentData);
    },
    loading(newval) {
      if (newval == false) {
        this.TotalHeight = 0;
        this.HeightArr = [];
      }
    },
    "contentData.length"() {
      this.timer = setTimeout(() => {
        this.getFollowedData();
        this.timer = null;
      }, 100);
    },
  },
  created() {
    this.getDynamicFollowContentData(this.dataId);
  },
  activated() {
    //定时器延迟拿取数据
    this.timer = setTimeout(() => {
      this.getFollowedData();
      this.timer = null;
    }, 100);
  },
  methods: {
    //获取数据
    getDynamicFollowContentData(dataparam) {
      getDynamicFollowContent({ axios: this.$axios }, dataparam).then(
        ({ data }) => {
          // console.log(data.data);
          data.data.forEach((item) => {
            item["isAutoplay"] = false;
            item["isFollow"] = false;
            item["isLike"] = false;
            let ZhReg = /^\{/;
            let res = ZhReg.test(item.content);
            if (res) {
              let Zh = item.content
                .split(`"qiushi_content": "`)[1]
                .slice(0, -2);
              item.content = unescape(Zh.replace(/\\/g, "%"));
            }
          });
          this.contentData.push(...data.data);
        }
      );
    },
    //点赞
    toLike(item) {
      item.isLike = !item.isLike;
      if (item.isLike) {
        item.like_count++;
      } else {
        item.like_count--;
      }
    },
    //关注
    getFollowState(item) {
      item.isFollow = !item.isFollow;
      let followedData = JSON.parse(
        window.localStorage.getItem("followedData")
      );
      let idArr = [];
      followedData.forEach((item) => {
        idArr.push(item.id);
      });
      if (item.isFollow && idArr.indexOf(item.user.id) == -1) {
        followedData.push(item.user);
        window.localStorage.setItem(
          "followedData",
          JSON.stringify(followedData)
        );
      } else {
        // console.log(followedData.indexOf(item.user));
        followedData.forEach((item2, index) => {
          if (item2.id == item.user.id) {
            followedData.splice(index, 1);
            window.localStorage.setItem(
              "followedData",
              JSON.stringify(followedData)
            );
          }
        });
      }
    },
    //分享
    addShareCount(item) {
      item.share_count++;
    },
    //删除
    deleteData(item) {
      this.contentData.forEach((item2, index) => {
        if (item2 == item) {
          let confi = confirm("确定要删除吗?");
          if (confi) {
            this.contentData.splice(index, 1);
          }
        }
      });
    },
    //获取所有内容的高度
    getHeight(val) {
      this.TotalHeight += val;
      if (this.dataId < 2) {
        this.HeightArr.push(val);
      } else {
        this.refreshData.push(val);
      }
    },
    //获取已关注
    getFollowedData() {
      let followedData = JSON.parse(
        window.localStorage.getItem("followedData")
      );
      let followedDataId = [];
      followedData.forEach((item) => {
        followedDataId.push(item.id);
      });
      this.contentData.forEach((item) => {
        if (followedDataId.indexOf(item.user.id) != -1) {
          item.isFollow = true;
        } else {
          item.isFollow = false;
        }
      });
    },
    //跳转评论详情页
    goTocomment(item) {
      console.log(item);
      window.localStorage.setItem("commentContentData", JSON.stringify(item));
      let dataId = item.id;
      if (dataId != 125654473 && dataId != 125657703) {
        dataId = 125654473;
      }
      this.$router.push(`/comment?id=${dataId}`);
    },
  },
};
</script>

<style lang="scss" scoped>
.dynamic-follow-content-view {
  width: 100%;
  margin-top: 65px;
  padding: 10px 10px 50px;
  box-sizing: border-box;
  .top-tip {
    width: 100%;
    height: 122px;
    text-align: center;
    position: relative;
    border-bottom: 1px solid #ddd;
    margin-bottom: 10px;
    .top-img {
      width: 350px;
      height: 100px;
      margin: -10px auto 0;
      background: url("@/assets/images/home/follow/Snipaste_2022-11-23_16-25-35.jpg")
        no-repeat center center;
      background-size: cover;
      transform: scale(0.7);
    }
    .top-text-box {
      width: 100%;
      text-align: center;
      .top-text-black {
        font-size: 16px;
        font-weight: 600;
        margin-bottom: 20px;
        color: #666;
      }
      .top-text-lightblack {
        color: #aaa;
      }
    }
  }
}
</style>