
//动态隔壁api
export const getDynamicNearbyContent = ({axios},data) => {
    return axios.get(`/JSON/dynamic/nearby${data}.json`)
}
//动态关注api
export const getDynamicFollowContent = ({axios},data) => {
    return axios.get(`/JSON/dynamic/follow${data}.json`)
}
//动态话题api
export const getDynamicTopicsContent = ({axios},data) => {
    return axios.get(`/JSON/dynamic/topics${data}.json`)
}